import React, { useState, useEffect } from 'react';
import {Redirect, useHistory } from 'react-router-dom';

import {Image} from "./View"

function Create() {

    const [fields, setFields] = useState({"sdo": false});
    const [data, setData] = useState("s");
    const [imageData, setImageData] = useState("s");

    const history = useHistory();

    var handleChange = (key, isBool) => {
    
        return (e) => {
            console.log(e.target.value);

            var toMerge = {};
            if(isBool) {
                console.log(e.target.checked);
                toMerge[key]=e.target.checked ? true: false;
            } else {
                toMerge[key]=e.target.value;
            }

            setFields({...fields, ...toMerge});
        }
    }

    var onClick = (e) => {
        history.push(`/view?data=${data}`);
    }

    useEffect(()=> {
        console.log(fields);
        var b64Data = btoa(JSON.stringify(fields));
        setData(b64Data);
        setImageData(`${window.location.origin}/${window.location.pathname.split('/').slice(1, -1).join('/')}/confirm?data=${b64Data}`);
    }, [fields])


    return (
        <div>
            <div className="data">
                <Input label="First Name" handleChange={handleChange("fn")}/>
                <Input label="Last Name" handleChange={handleChange("ln")}/>
                <Input label="DOB" handleChange={handleChange("dob")} type="date"/>
                <Input label="Single Dose?" handleChange={handleChange("sdo", true)} type="checkbox"/>
                <Input label="Lot#" handleChange={handleChange("lnum")}/>
                <Input label="1st Dose" handleChange={handleChange("fddate")} type="date"/>
                {!fields.sdo && <Input label="2nd Dose" handleChange={handleChange("sddate")} type="date"/>}
                
            </div>
            <div>
                <br/>
                <br/>
                <br/>
                <button onClick={onClick}>Create Pass</button>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <Image data={imageData} />
            </div>
        </div>

    );

}

function Input(props) {
    return (
        <React.Fragment>
            <label>{`${props.label}: `}</label>
            <input type={props.type||"text"} onChange={props.handleChange}/>
        </React.Fragment>
    );
}

export default Create;