import React, { useState, useEffect } from 'react';
import querystring from "querystring"

function Confirm() {

    const keys = [
        {key: "fn", value: "First Name"},
        {key: "ln", value: "Last Name"},
        {key: "dob", value: "DOB"},
        {key: "sdo", value: "Single Dose?"},
        {key: "lnum", value: "Lot#"},
        {key: "fddate", value: "1st Dose"},
        {key: "sddate", value: "2nd Dose", dependsOn:"sdo"}
    ]

    const [data, setData] = useState({});

    useEffect(()=> {
        var dataB64 = querystring.parse(window.location.search.split('?')[1])["data"];
        var dataObj = JSON.parse(atob(dataB64));
        setData({...data, ...dataObj})
    }, [])

    return (
        <div className="data view">
            {
            keys.map((key) => {
                return <React.Fragment>
                    <label>{key.value}:</label>
                    <div>{data[key.key] != undefined ? String(data[key.key]) : "N/A"}</div>
                </React.Fragment>
            })
            }
        </div>
    );

}

export default Confirm;