import qrcode from "qrcode"
import querystring from "querystring"

import React, { useState, useEffect } from 'react';

function Image(props) {

    const qrOption = {
        margin: 2,
        width: 175
    }

    const [image, setImage] = useState("data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==");

    useEffect(async () => {
        if(props.data) {
            const bufferImage = await qrcode.toDataURL(props.data,qrOption);
            setImage(bufferImage);
        }
    }, [props.data]);
    

    return (
        <div>

            <img src={image} />
        </div>
    );

}

function View() {

    const keys = [
        {key: "fn", value: "First Name"},
        {key: "ln", value: "Last Name"},
        {key: "dob", value: "DOB"}
    ]

    const [url, setUrl] = useState("");
    const [data, setData] = useState({});

    useEffect(()=> {
        var dataB64 = querystring.parse(window.location.search.split('?')[1])["data"];
        var dataObj = JSON.parse(atob(dataB64));
        var url = `${window.location.origin}/${window.location.pathname.split('/').slice(1, -1).join('/')}/confirm?data=${dataB64}`;
        console.log(dataObj);
        setData({...dataB64, ...dataObj});
        setUrl(url);
    }, [])

    return (
        <div>
            <div className="data view">
                {
                keys.map((key) => {
                    return <React.Fragment>
                        <label>{key.value}:</label>
                        <div>{data[key.key] != undefined ? String(data[key.key]) : "N/A"}</div> 
                    </React.Fragment>
                })
                }
            </div>
            <Image data={url}/>
        </div>

    );
}

export default View;
export {Image};